import React, { useEffect } from 'react';
import html2canvas from 'html2canvas';
import './index.css';
import { TwitterIcon, TwitterShareButton } from 'react-share';

const dataURLtoFile = (dataurl, fileName) => {
  var arr = dataurl.split(','),
    mime = arr[0].match(/:(.*?);/)[1],
    bstr = atob(arr[1]),
    n = bstr.length,
    u8arr = new Uint8Array(n);

  while (n--) {
    u8arr[n] = bstr.charCodeAt(n);
  }

  return new File([u8arr], fileName, { type: mime });
};

async function shareKakao() {
  window.scrollTo(0, 0);
  const file = await html2canvas(document.getElementById('test')).then(
    (canvas) => {
      const file = dataURLtoFile(canvas.toDataURL('image/png'), 'image.png');
      return file;
    }
  );
  const kakao = window.Kakao;
  if (!kakao.isInitialized()) {
    kakao.init('fa0b6b3e890ec130a3040742afd1cd6b');
  }
  const imageUrl = await kakao.Share.uploadImage({
    file: [file],
  }).then(function (res) {
    const imageUrl = res.infos.original.url;
    return imageUrl;
  });
  return kakao.Share.sendDefault({
    objectType: 'feed',
    content: {
      title: '',
      description: `   ?`,
      imageUrl: imageUrl,
      link: {
        webUrl: 'http://localhost:3000/record',
        mobileWebUrl: 'http://localhost:3000/record',
        androidExecParams: 'test',
      },
    },
    buttons: [
      {
        title: ' ',
        link: {
          webUrl: 'http://localhost:3000/record',
          mobileWebUrl: 'http://localhost:3000/record',
        },
      },
    ],
  });
}

const Record = () => {
  let now = new Date();
  useEffect(() => {
    const script = document.createElement('script');
    script.src = 'https://developers.kakao.com/sdk/js/kakao.js';
    script.async = true;

    document.body.appendChild(script);
    return () => {
      document.body.removeChild(script);
    };
  }, []);
  return (
    <div className="background_split">
      <div className="header">
        <div
          style={{
            width: '30px',
            height: '30px',
            borderRadius: '50%',
            backgroundColor: '#EDF5F0',
          }}
        ></div>
      </div>
      <button onClick={shareKakao}></button>
      <TwitterShareButton
        style={{ marginRight: '20px' }}
        url={'https://zhtmahthgus.github.io/record'}
      >
        <TwitterIcon size={48} round={true} borderRadius={24}></TwitterIcon>
      </TwitterShareButton>

      <p className="title1">   </p>
      <p className="title2">  </p>
      <div id="test" className="wrapper">
        <div className="content_top">
          <p className="date">{now.getDate()}</p>
          <div className="content_top_left">
            <div
              style={{
                width: '30px',
                height: '30px',
                borderRadius: '50%',
                backgroundColor: '#54B877',
              }}
            ></div>
            <div
              style={{
                width: '30px',
                height: '30px',
                borderRadius: '50%',
                backgroundColor: '#54B877',
              }}
            ></div>
          </div>
        </div>
        <hr className="line_horizontal" />
        <div className="content_bottom">
          <div
            style={{ width: '20vw', height: '20vw', backgroundColor: 'grey' }}
          ></div>
          <div className="content_bottom_right">
            <div className="content_bottom_right_top">
              <div
                style={{
                  width: '40px',
                  height: '40px',
                  borderRadius: '50%',
                  backgroundColor: '#54B877',
                }}
              ></div>
              <div className="temperature">
                <p className="temperature_value">20</p>
                <p className="temperature_sign">C</p>
              </div>
              <hr className="line_vertical" />
              <p className="temperature_info">: 24 / : 25</p>
              <p className="temperature_info">: 0%</p>
            </div>
            <textarea className="content_bottom_right_bottom" />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Record;
